@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
* {
    margin: 0;
    padding: 0;
    outline: 0;
    list-style: none;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}

html, body, #root {
    height: 100%;
}

body {
    background: -webkit-gradient(linear, left top, left bottom, from(rgb(0, 0, 0, 0.05)), to(rgb(0, 0, 0, 0.05))), url(/uirapuru-web/static/media/background.5474b6a0.webp) no-repeat;
    background: linear-gradient(rgb(0, 0, 0, 0.05), rgb(0, 0, 0, 0.05)), url(/uirapuru-web/static/media/background.5474b6a0.webp) no-repeat;
    background-size: cover;
    -webkit-font-smoothing: antialiased !important;
}

body, input, button {
    font-family: 'Roboto', sans-serif;
}

::-webkit-scrollbar {
    width: 5px;
}

::-webkit-scrollbar-track-piece {
    background-color: #fff;
}

::-webkit-scrollbar-thumb:vertical {
    height: 5px;
    background-color: #555;
}

::-webkit-scrollbar-thumb:horizontal {
    width: 5px;
    background-color: #555;
}

